import React from "react";
import "./App.css";
import { TransitionGroup, CSSTransition } from "react-transition-group";
import { Route, Switch } from "react-router-dom";
import Navbar from "../src/components/navbar/Navbar";
import Home from "../src/components/home/Home";
import About from "../src/components/about/About";
import StartProject from "../src/components/startproject/StartProject";
import Work from "../src/components/work/Work";
import Contact from "../src/components/contact/Contact";
import Sliding from "./components/slider/Sliding";
import Form from "./components/contact/Form";

const clearPadding = {
  paddingRight: "0px",
  paddingLeft: "0px"
};
class App extends React.Component {
  constructor(props) {
    super(props);
  }
  // scrollToTop = () => {
  //   setTimeout(() => {
  //     window.scrollTo(0, 0);
  //     if (document.querySelector(".fade-enter-done")) {
  //       let viewHeight = document.querySelector(".fade-enter-done")
  //         .scrollHeight;
  //       document.querySelector(".page").style.height = viewHeight;
  //     }
  //   }, 500);
  // };
  render() {
    console.log(this.props);
    return (
      <div className="container-fluid" style={clearPadding}>
        <Navbar />
        <Route
          render={({ location }) => (
            <TransitionGroup>
              <CSSTransition
                key={location.key}
                timeout={300}
                classNames="fade"
                //onEntering={this.scrollToTop}
              >
                <Switch location={location}>
                  <Route exact path="/" component={Home} />
                  <Route path="/about" component={About} />
                  <Route path="/slide" component={Sliding} />
                  <Route path="/work" component={Work} />
                  <Route path="/contact" component={Contact} />
                  <Route path="/start" component={StartProject} />
                  <Route path="/start:slug" component={StartProject} />
                  <Route path="/form" component={Form} />
                </Switch>
              </CSSTransition>
            </TransitionGroup>
          )}
        />
      </div>
    );
  }
}

export default App;
