import React from "react";

const Footer = () => {
  return (
    <div className="row footer-main">
      <div className="col-md-3 logo">
        <img
          className="img-responsive"
          src="./images/logo.png"
          alt="logo image"
        />
      </div>
      <div className="col-md-3 email">
        <span>hello@shaokhancreatives.com</span>
      </div>
      <div className="col-md-3 phone">
        <span>+234 905 685 7532</span>
      </div>
      <div className="col-md-3 social-media">
        <a href="https://twitter.com" target="_blank">
          <img
            src="./images/twitter.png"
            className="img-responsive"
            alt="twitter"
          />
        </a>
        <a href="https://instagram.com" target="_blank">
          <img
            src="./images/instagram.png"
            className="img-responsive"
            alt="instagram"
          />
        </a>
        <a href="https://facebook.com" target="_blank">
          <img
            src="./images/facebook.png"
            className="img-responsive"
            alt="facebook"
          />
        </a>
        <a href="https://earth.com" target="_blank">
          <img
            src="./images/earth.png"
            className="img-responsive"
            alt="earth"
          />
        </a>
        <a href="https://bee.com" target="_blank">
          <img src="./images/bee.png" className="img-responsive" alt="bee" />
        </a>
      </div>
    </div>
  );
};

export default Footer;
