import React from "react";
import Slider from "react-slick";

const settings = {
  dots: true,
  infinite: true,
  speed: 500,
  slidesToShow: 3,
  arrows: false,
  focusOnSelect: false,
  cssEase: "easein",
  swipeToSlide: true,
  swipe: true,
  slidesToScroll: 1
};
const Sliding = () => {
  return (
    <div className="container-fluid slider">
      <Slider {...settings}>
        <div>
          <img src="./images/2koil.png" alt="David Okeke" />
        </div>
        <div>
          <img src="./images/fluxUI.png" alt="David Okeke" />
        </div>
        <div>
          <img src="./images/larae.png" alt="David Okeke" />
        </div>
        <div>
          <img src="./images/cosgroove.png" alt="David Okeke" />
        </div>
      </Slider>
    </div>
  );
};

export default Sliding;
