import React from "react";
import Footer from "../footer/Footer";

let pushUrl = "/start/step-2";
const renderSwitch = url => {
  console.log(url);
  switch (url) {
    case "/start/step-2":
      pushUrl = "/start/step-3";
      return (
        <div>
          <span className="form-progress">2/5</span>
          <h1 className="question">Nice to Meet you</h1>
          <div className="form-group">
            <input
              className="form-control"
              type="text"
              placeholder="How Can we help you?"
            />
          </div>
        </div>
      );
      break;

    case "/start/step-3":
      pushUrl = "/start/step-4";
      return (
        <div>
          <span className="form-progress">3/5</span>
          <h1 className="question">Sounds like a plan</h1>
          <div className="form-group">
            <input
              className="form-control"
              type="text"
              placeholder="What's your budget?"
            />
          </div>
        </div>
      );
      break;

    case "/start/step-4":
      pushUrl = "/start/step-5";
      return (
        <div>
          <span className="form-progress">4/5</span>
          <h1 className="question">Good things always have a price</h1>
          <div className="form-group">
            <input
              className="form-control"
              type="text"
              placeholder="What's your deadline?"
            />
          </div>
        </div>
      );
      break;

    case "/start/step-5":
      return (
        <div>
          <span className="form-progress">5/5</span>
          <h1 className="question">We are glad it's not yesterday!</h1>
          <div className="form-group">
            <input
              className="form-control"
              type="text"
              placeholder="What's your email?"
            />
          </div>
        </div>
      );
      break;

    default:
      return (
        <div>
          <span className="form-progress">1/5</span>
          <h1 className="question">Let's Make the first Step</h1>
          <div className="form-group">
            <input
              className="form-control"
              type="text"
              placeholder="What's your name"
            />
          </div>
        </div>
      );
      break;
  }
};
const StartProject = props => {
  console.log(props);
  return (
    <div className="container-fluid start-page page">
      <div className="row nav-container">
        <div className="col-md-1"></div>
        <div className="col-md-11">
          <div className="row">
            <div className="col-md-10 stage">
              {renderSwitch(props.location.pathname)}
              {props.location.pathname !== "/start/step-5" ? (
                <img
                  src="/images/next.png"
                  alt="next page"
                  onClick={() => props.history.push(pushUrl)}
                />
              ) : (
                <button>next</button>
              )}
            </div>
          </div>
        </div>
        <div className="col-md-12">
          <Footer />
        </div>
      </div>
    </div>
  );
};

export default StartProject;
