import React from "react";
import { Link } from "react-router-dom";

class Navbar extends React.Component {
  constructor(props) {
    super(props);

    this.wrapper = React.createRef();
  }

  wrapperRef = () => {
    this.wrapper.current.ownerDocument.body.style.backgroundColor = "white";

    console.log(this.wrapper.current.ownerDocument);
  };
  render() {
    return (
      <div ref={this.wrapper}>
        <header>
          <section className="navigation">
            <div className="nav-container">
              <div className="brand">
                <Link to="/">
                  <img className="logo" src="./images/logo.png" alt="logo" />
                </Link>
              </div>
              <nav className="">
                <div className="nav-mobile">
                  <img
                    className="img-responsive waving-hand"
                    src="./images/waving-hand.png"
                    alt="start project"
                  />
                  <a id="nav-toggle" href="#!">
                    <span></span>
                  </a>
                </div>
                <ul className="nav-list">
                  <img
                    className="hide-desktop"
                    src="./images/logo.png"
                    alt="mobile logo"
                    style={{
                      width: "20%",
                      position: "relative",
                      bottom: "11px"
                    }}
                  />
                  <li>
                    <Link to="/about">About</Link>
                  </li>
                  <li>
                    <Link to="/work">Works</Link>
                  </li>

                  <li>
                    <Link to="/contact">Contact</Link>
                  </li>

                  <li>
                    {/* <img
                      className="img-fluid wave"
                      src="./images/waving-hand.png"
                      alt="start project"
                    /> */}
                    <Link to="/start">Start a Project</Link>
                  </li>
                  <li>
                    <Link to="/buffet" onClick={this.wrapperRef}>
                      Design Buffet
                    </Link>
                  </li>
                </ul>
              </nav>
            </div>
          </section>
        </header>
      </div>
    );
  }
}

export default Navbar;
