import React from "react";
import Sliding from "../slider/Sliding";

const Work = () => {
  return (
    <div className="container-fluid work-page page">
      <div className="row nav-container">
        <div className="col-md-12 col-sm-offset-1">
          <div className="row">
            <div className="col-md-7">
              <h2 className="header">Our Work</h2>
              <div className="caption">
                A creative agency outfit focused on branding and visual
                communication, which combines strategy, design and interactive
                experience to set our clients apart from their competitors while
                creating meaningful things that brands reflect.
              </div>
            </div>
          </div>
          <div className="row">
            <Sliding />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Work;
